import axios from "axios";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import baseUrl from "../AppConfig";

class SvenciuForm extends Component {
  state = {
    id: "",
    pavadinimas: "",
    aprasymas: "",
    paveiksliukas: "",
    tipas: "",
    vlastybes: [],
  };
  async componentDidMount() {
    const id = this.props.match.params.id;
    if (id === 0) return;

    await axios.get(baseUrl + "/api/sventes/" + id).then((res) =>
      this.setState({
        id,
        pavadinimas: res.data.pavadinimas,
        aprasymas: res.data.aprasymas,
        paveiksliukas: res.data.paveiksliukas,
        tipas: res.data.tipas,
      })
    );
    console.log(this.state);
    // );
  }

  handleChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handleSubmit = (e) => {
    e.preventDefault();
    const { id, pavadinimas, aprasymas, paveiksliukas, tipas } = this.state;
    if (id < 1) {
      axios
        .post(baseUrl + "/api/sventes/", {
          pavadinimas,
          aprasymas,
          paveiksliukas,
          tipas,
        })
        .then(() => this.props.history.push("/"));
    } else {
      axios
        .put(baseUrl + "/api/sventes/" + id, {
          pavadinimas,
          aprasymas,
          paveiksliukas,
          tipas,
        })
        .then(() => this.props.history.push("/"));
    }
  };

  render() {
    const { pavadinimas, aprasymas, paveiksliukas, tipas } = this.state;

    return (
      <div className="m-5">
        <form onSubmit={(e) => this.handleSubmit(e)}>
          <div className="form-group">
            <label>Pavaidinimas</label>
            <input
              name="pavadinimas"
              onChange={(e) => this.handleChange(e)}
              defaultValue={pavadinimas}
              type="text"
              className="form-control"
              id="pavadinimas"
            />
          </div>
          <div className="form-group">
            <label>Aprasymas</label>
            <input
              name="aprasymas"
              onChange={(e) => this.handleChange(e)}
              defaultValue={aprasymas}
              type="text"
              className="form-control"
              id="aprasymas"
            />
          </div>
          <div className="form-group">
            <label>Paveiksliukas URL</label>
            <input
              name="paveiksliukas"
              onChange={(e) => this.handleChange(e)}
              defaultValue={paveiksliukas}
              type="text"
              className="form-control"
              id="paveiksliukas"
            />
          </div>
          <div className="form-group">
            <label>Tipas</label>
            <input
              name="tipas"
              onChange={(e) => this.handleChange(e)}
              defaultValue={tipas}
              type="text"
              className="form-control"
              id="tipas"
            />
          </div>
          <button type="submit" className="btn btn-primary">
            Submit
          </button>
          <Link to="/" className="btn btn-danger btn-sm m-3">
            Cancel
          </Link>
        </form>
      </div>
    );
  }
}

export default SvenciuForm;
