import axios from "axios";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import baseUrl from "../AppConfig";

class SvenciuList extends Component {
  state = {
    sventes: [],
  };

  async componentDidMount() {
    await axios.get(baseUrl + "/api/sventes").then((res) => {
      const sventes = res.data;
      this.setState({ sventes });
    });
  }

  handleDelete = async (id) => {
    console.log(id);
    const status = await (await axios.delete(baseUrl + "/api/sventes/" + id))
      .status;
    if (status && window.location.reload());
    // window.location.reload();
  };

  render() {
    const { sventes } = this.state;
    console.log(sventes);
    return (
      <React.Fragment>
        <div className="row row-cols-1 row-cols-md-3">
          {sventes.map((svente) => (
            <div className="col mb-4">
              <div className="card">
                <img
                  src={svente.paveiksliukas}
                  className="card-img-top"
                  alt={svente.paveiksliukas}
                />
                <div className="card-body">
                  <h5 className="card-title">{svente.pavadinimas}</h5>
                  <p className="card-text">{svente.aprasymas}</p>
                </div>
                <div className="card-footer">
                  <small className="text-muted">{svente.tipas}</small>
                </div>
                <Link
                  to={"/sventes/" + svente.id}
                  className="btn btn-info btn-sm m-2"
                >
                  Update
                </Link>
                <button
                  onClick={() => this.handleDelete(svente.id)}
                  className="btn btn-danger btn-sm m-2"
                >
                  Delete
                </button>
              </div>
            </div>
          ))}
        </div>
      </React.Fragment>
    );
  }
}

export default SvenciuList;
