import "./App.css";
import SvenciuList from "./components/SvenciuList";
import "bootstrap/dist/css/bootstrap.min.css";
import NavBar from "./components/NavBar";
import React from "react";
import { Route, Switch } from "react-router-dom";
import SvenciuForm from "./components/SvenciuForm";
import ValstybesList from "./components/ValstybesList";
import ValstybiuForm from "./components/ValstybiuForm";

function App() {
  return (
    <React.Fragment>
      <NavBar />
      <div className="container m-3">
        <Switch>
          <Route path="/sventes/:id" component={SvenciuForm} />
          <Route path="/valstybes/:id" component={ValstybiuForm} />

          <Route exact path="/" component={SvenciuList} />
          <Route path="/sventes" component={SvenciuList} />
          <Route path="/valstybes" component={ValstybesList} />
        </Switch>
      </div>
    </React.Fragment>
  );
}

export default App;
