import axios from "axios";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import baseUrl from "../AppConfig";

class ValstybesList extends Component {
  state = {
    valstybes: [],
  };

  async componentDidMount() {
    await axios.get(baseUrl + "/api/valstybes").then((res) => {
      const valstybes = res.data;
      this.setState({ valstybes });
    });
  }

  handleDelete = async (id) => {
    console.log(id);
    const status = await (await axios.delete(baseUrl + "/api/valstybes/" + id))
      .status;
    if (status && window.location.reload());
    // window.location.reload();
  };

  render() {
    const { valstybes } = this.state;
    console.log(valstybes);
    return (
      <React.Fragment>
        <div className="row row-cols-1 row-cols-md-3">
          {valstybes.map((valstybe) => (
            <div key={valstybe.id} className="col mb-4">
              <div className="card">
                <img
                  src={valstybe.veliavosNuotrauka}
                  className="card-img-top"
                  alt={valstybe.veliavosNuotrauka}
                />
                <div className="card-body">
                  <h5 className="card-title">{valstybe.pavadinimas}</h5>
                  <p className="card-text">{valstybe.prezidentas}</p>
                </div>
                <Link
                  to={"/valstybes/" + valstybe.id}
                  className="btn btn-info btn-sm m-2"
                >
                  Update
                </Link>
                <button
                  onClick={() => this.handleDelete(valstybe.id)}
                  className="btn btn-danger btn-sm m-2"
                >
                  Delete
                </button>
              </div>
            </div>
          ))}
        </div>
      </React.Fragment>
    );
  }
}

export default ValstybesList;
