import axios from "axios";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import baseUrl from "../AppConfig";

class ValstybiuForm extends Component {
  state = {
    id: 0,
    pavadinimas: "",
    veliavosNuotrauka: "",
    prezidentas: "",
  };
  async componentDidMount() {
    const id = this.props.match.params.id;
    if (id === 0) return;

    await axios.get(baseUrl + "/api/valstybes/" + id).then((res) =>
      this.setState({
        id,
        pavadinimas: res.data.pavadinimas,
        veliavosNuotrauka: res.data.veliavosNuotrauka,
        paveiprezidentasksliukas: res.data.prezidentas,
      })
    );
    console.log(this.state);
    // );
  }

  handleChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handleSubmit = (e) => {
    e.preventDefault();
    const { id, pavadinimas, veliavosNuotrauka, prezidentas } = this.state;
    if (id < 1) {
      axios
        .post(baseUrl + "/api/valstybes", {
          pavadinimas,
          veliavosNuotrauka,
          prezidentas,
        })
        .then(() => this.props.history.push("/"));
    } else {
      axios
        .put(baseUrl + "/api/valstybes/" + id, {
          pavadinimas,
          veliavosNuotrauka,
          prezidentas,
        })
        .then(() => this.props.history.push("/"));
    }
  };

  render() {
    const { pavadinimas, veliavosNuotrauka, prezidentas } = this.state;

    return (
      <div className="m-5">
        <form onSubmit={(e) => this.handleSubmit(e)}>
          <div className="form-group">
            <label>Pavaidinimas</label>
            <input
              name="pavadinimas"
              onChange={(e) => this.handleChange(e)}
              defaultValue={pavadinimas}
              type="text"
              className="form-control"
              id="pavadinimas"
            />
          </div>
          <div className="form-group">
            <label>Veliava</label>
            <input
              name="veliavosNuotrauka"
              onChange={(e) => this.handleChange(e)}
              defaultValue={veliavosNuotrauka}
              type="text"
              className="form-control"
              id="veliavosNuotrauka"
            />
          </div>
          <div className="form-group">
            <label>Prezidentas</label>
            <input
              name="prezidentas"
              onChange={(e) => this.handleChange(e)}
              defaultValue={prezidentas}
              type="text"
              className="form-control"
              id="prezidentas"
            />
          </div>
          <button type="submit" className="btn btn-primary">
            Submit
          </button>
          <Link to="/" className="btn btn-danger btn-sm m-3">
            Cancel
          </Link>
        </form>
      </div>
    );
  }
}

export default ValstybiuForm;
